import { useContext } from "react";
import { AuthContext } from "../context/AuthProvider";
import { Link } from "react-router-dom";

function Nav() {

	const { auth, setAuth } = useContext(AuthContext);

	return (
		<nav className="navbar navbar-expand-md navbar-dark bg-dark sticky-top" >
			<div className="container">

				<div className="navbar-collapse collapse justify-content-between" id="navbar-content">
					<ul className="navbar-nav mr-auto">

						{/*   */}
						<li className="nav-item">
							<Link className="nav-link" to="/"><i className="fas fa-home"></i> Home</Link>
						</li>

						{/*  */}
						<li className="nav-item dropdown">

							<div className="nav-link dropdown-toggle" id="navbarDropdown"
								role="button" data-toggle="dropdown" aria-haspopup="true"
								aria-expanded="false"></div>

							<div className="dropdown-menu" aria-labelledby="navbarDropdown">
								<Link className="dropdown-item" to="/bbslist"></Link>
								<Link className="dropdown-item" to="/bbswrite"></Link>
							</div>
						</li>
					</ul>
					<ul className="navbar-nav ml-auto">

						{							
							(auth) ?
								<>
									{/*   */}
									<li className="nav-item">
										<span className="nav-link"> {auth}  <i className="fab fa-ello"></i> &nbsp; </span>
									</li>

									{/*  */}
									<li className="nav-item">
										<Link className="nav-link" to="/logout"><i className="fas fa-sign-out-alt"></i> </Link>
									</li>

								</>
								:
								<>
									{/*  */}
									<li className="nav-item">
										<Link className="nav-link" to="/login"></Link>
									</li>

									{/*  */}
									<li className="nav-item">
										<Link className="nav-link" to="/join"></Link>
									</li>
								</>
						}

					</ul>
				</div>
			</div>
		</nav>
	);
}

export default Nav;